import { FontColor } from 'core/types/common';

/**
 * Возвращает цвет для метки в зависимости от параметров.
 *
 * @param {boolean} [disabled] - Флаг, указывающий, является ли элемент disabled.
 * @param {FontColor} [color='primary'] - Цвет.
 * @returns {FontColor} - Возращает цвет в зависимости от disabled.
 */
const getLabelColor = (disabled?: boolean, color: FontColor = 'primary'): FontColor => (disabled ? 'disabled' : color);

export default getLabelColor;
